/*начало оформления кнопки  меню для адаптива*/
.toggle-mnu {
    line-height: 48px;
    position: absolute;
    top: 0;
    right: 10px;
    display: block;
    float: right;
    width: 28px;
    height: 28px;
    margin-top: 35px;
    margin-right: 15px;
    text-align: center;
    color: #fff;
}

.toggle-mnu span:after, .toggle-mnu span:before {
    position: absolute;
    top: 9px;
    left: 0;
    content: '';
}

.toggle-mnu span:after {
    top: 18px;
}

.toggle-mnu span {
    position: relative;
    display: block; }

.toggle-mnu span, .toggle-mnu span:after, .toggle-mnu span:before {
    width: 100%;
    height: 2px;
    transition: all .3s;
    border-radius: 2px;
    background-color: #fff;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.toggle-mnu.on span {
    background-color: transparent;
}

.toggle-mnu.on span:before {
transform: rotate(45deg) translate(-1px, 0);
}

.toggle-mnu.on span:after {
 transform: rotate(-45deg) translate(6px, -7px);
}

.toggle-mnu.on + #menu {
    visibility: visible;
    opacity: 1; }

