.forma-header p
{
    font-size: .938rem;

    margin-top: 22px;

    text-align: left;

    color: #666;
}

.forms-call label
{
    display: block;

    margin: 10px 0;
}

.forms-call input,
.forms-call textarea
{
    font-size: .8rem;
    line-height: 30px;

    display: block;

    width: 100%;
    height: 30px;
    margin: 10px auto;

    -webkit-transition: all .5s ease .1s;
       -moz-transition: all .5s ease .1s;
            transition: all .5s ease .1s;
    text-indent: 10px;

    color: #364052;
    border: none;
    border-bottom: 1px solid #dc143c;
}

.forms-call textarea
{
    overflow: hidden;

    height: 50px;

    resize: none;
}

.forms-call input:focus,
.forms-call textarea:focus
{
    color: #222833;
    border-bottom: 1px solid #0cb4ce;
}

.forms-call .get-buttons
{
    font-size: .938rem;

    margin-top: 10px;
    padding: 10px 50px;

    -webkit-transition: all .5s ease .1s;
       -moz-transition: all .5s ease .1s;
            transition: all .5s ease .1s;
    text-decoration: none;

    color: #fff;
    border: 0;
    border: 1px solid  #dc143c;
    background-color: #dc143c;
}

.button-call
{
    float: left;

    margin: 0;

    text-align: center;
}

.forms-call .get-buttons:focus,
.forms-call .get-buttons:hover,
.forms-call .get-buttons:active
{
    color: #0cb4ce;
    border: 1px solid #0cb4ce;
    background-color: transparent;
}

/* конец формы обратной связи */
/*оформление всплывающей формы а ниже и благодарности */
.forms-calldecor
{
    position: relative;

    overflow: hidden;

    max-width: 480px;
    margin: auto;

    -webkit-transition: all .5s ease .1s;
       -moz-transition: all .5s ease .1s;
            transition: all .5s ease .1s;

    border: 1px solid #fff;
    border-radius: 1em;
    background-color: rgba(0, 0, 0, .5);
}

.forms-calldecor h4,
.forms-calldecor h5
{
    font-size: 1.3em;

    margin: 50px 20px 0;

    text-align: center;

    color: #fff;
}

.forms-calldecor h5
{
    font-size: .96em;

    margin-top: 10px;
    margin-bottom: 30px;

    letter-spacing: 1px;
}

.forms-calldecor input,
.forms-calldecor textarea
{
    width: 90%;

    color: white;
}

.forms-calldecor .button-call
{
    float: none;

    margin-bottom: 40px;

    text-align: center;

    color: #fff;
}

.forms-calldecor .mfp-close
{
    position: absolute;
    top: 15px;
    right: 15px;

    border-radius: 3px;
}

.forms-calldecor .mfp-close-btn-in .mfp-close
{
    font-size: 1.5em;
    font-weight: bold;

    color: #fff;
    border-radius: 3px;
}

.forms-calldecor .success
{
    position: absolute;
    z-index: 1;
    top: -100%;
    left: 0;

    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display:         flex;

    width: 100%;
    height: 100%;
    padding-right: 20px;
    padding-left: 20px;

    -webkit-transition: all .5s ease .1s;
       -moz-transition: all .5s ease .1s;
            transition: all .5s ease .1s;

    opacity: 0;
    background-color: #000;
}

.forms-calldecor .success.active
{
    top: 0;

    opacity: 1;
}

.forms-calldecor .success p
{
    font-size: 20px;

    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display:         flex;

    margin: auto;

    text-align: center;
    text-transform: uppercase;

    color: #fff;
}

/* overlay at start */
.mfp-fade.mfp-bg
{
    -webkit-transition: all .615s ease-out;
       -moz-transition: all .615s ease-out;
            transition: all .615s ease-out;

    opacity: 0;
}

/* overlay animate in */
.mfp-fade.mfp-bg.mfp-ready
{
    opacity: .8;
}

/* overlay animate out */
.mfp-fade.mfp-bg.mfp-removing
{
    opacity: 0;
}

/* content at start */
.mfp-fade.mfp-wrap .mfp-content
{
    opacity: 0;
}

/* content animate it */
.mfp-fade.mfp-wrap.mfp-ready .mfp-content
{
    -webkit-transition: all .615s ease-out;
       -moz-transition: all .615s ease-out;
            transition: all .615s ease-out;
    -webkit-transform: translateY(1%);
       -moz-transform: translateY(1%);
        -ms-transform: translateY(1%);
         -o-transform: translateY(1%);
            transform: translateY(1%);

    opacity: 1;
}

/* content animate out */
.mfp-fade.mfp-wrap.mfp-removing .mfp-content
{
    -webkit-transform: translateY(-120%);
       -moz-transform: translateY(-120%);
        -ms-transform: translateY(-120%);
         -o-transform: translateY(-120%);
            transform: translateY(-120%);

    opacity: 0;
}

